import styled from 'styled-components';

const Title = styled.h1`
	position: relative;
	max-width: 100%;
	margin: 0;
	padding: .8em 1em 0;
	color: inherit;
	font-size: 1.875em;
	font-weight: 600;
	text-align: center;
	text-transform: none;
	word-wrap: break-word;
`;

export default Title;
